@import "../../SCSSVariables.scss";

.BrandIntroduction {
  position: fixed;
  top: $ContactBarHeight;
  left: 0;
  height: 100 - $ContactBarHeight - $NavigationBarHeight;
  width: 100%;
  overflow: hidden;
  // 樓書swiper
  .BuildingBook {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    .swiper-container {
      height: 100%;
      .swiperWrapper {
        height: 100%;
        white-space: nowrap;
        .swiper-slide {
          height: 100%;
        //   display: inline-block;
          display: block;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
    // 按鈕外框
    .menuContainer {
      position: absolute;
      top: 7%;
      right: 0;
      // width: 15%;
      height: 3.5%;
      z-index: 1000;
      .item {
        margin-bottom: 25%;
        height: 100%;
        position: relative;
        text-align: end;
        img {
          height: 100%;
        }
        // 未啟用時
        &.notActive {
          .activeButton {
            opacity: 0;
          }
          .notActiveButton {
            opacity: 1;
          }
        }
        .activeButton {
          position: relative;
          opacity: 1;
        }
        .notActiveButton {
          position: absolute;
          top: 0;
          right: 0;
          opacity: 0;
        }
        // 區域全景次選單、項目全景次選單
        .secondMenu {
          position: absolute;
          top: 0;
          left: 0;
          height: 90%;
          transform: translate(-130%, 0);
          & > div {
            position: relative;
            height: 100%;
            margin-bottom: 1rem;
            img {
              height: 100%;
            }
            // 白天/黑夜啟用
            &.active {
              .activeButton {
                opacity: 1;
                pointer-events: auto;
              }
              .notActiveButton {
                opacity: 0;
                pointer-events: none;
              }
            }
            // 白天/黑夜非啟用
            &.notActive {
              .activeButton {
                opacity: 0;
                pointer-events: none;
              }
              .notActiveButton {
                opacity: 1;
                pointer-events: auto;
              }
            }
          }
          .day {
          }
          .night {
          }
        }
      }
    }
}
