@import '../../SCSSVariables.scss';

.HomePage {
    position: fixed;
    top: $ContactBarHeight;
    left: 0;
    height: 100 - $ContactBarHeight - $NavigationBarHeight;
    width: 100%;
    img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
}